body {
  font-family: 'EB Garamond', serif;
  font-size: 13.5px;
}
h1 {
  font-family: 'Lobster', cursive;
  font-size: 40px;
}
h2,
h3 {
  font-family: 'EB Garamond', serif;
  font-size: 16.6px;
}
.menu-meal {
  font-family: 'EB Garamond', serif;
  font-size: 16.6px;
  letter-spacing: 2px;
}
.main-menu {
  padding-bottom: 2rem;
}
.main-menu .flex-box {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: row nowrap;
  padding: 0 0.625rem;
  background-color: white;
}
.main-menu .flex-box .item-1 {
  font-family: 'Lobster', cursive;
  font-size: 2rem;
  padding: 0 2rem;
  color: #333;
}
.main-menu .flex-box .item-2 {
  font-family: 'Lobster', cursive;
  font-size: 2rem;
  padding: 0 2rem;
  color: #333;
}
.menu-nav {
  display: flex;
  justify-content: space-between;
  align-content: top;
  flex-direction: row;
}
.menu-nav .sider {
  order: 1;
}
.menu-nav .content {
  order: 2;
}
.heading-title,
.menu-display-category {
  font-family: 'Lobster', cursive;
  text-align: center !important;
  font-size: 2rem;
  padding: 0.5rem;
  color: #333;
  line-height: 1.07;
  letter-spacing: -0.5px;
  cursor: pointer;
}
.underline-60:after,
.menu-display-category:after {
  text-align: center !important;
  box-sizing: border-box;
  width: 60px;
  margin: 14px auto;
  content: '';
  display: block;
  height: 4px;
}
.underline-60:after {
  background-color: #262626;
}
.ant-layout-sider,
.ant-layout-sider-children {
  background-color: transparent;
  max-width: 256px;
  min-width: 256px;
}
.top-padding {
  margin-top: 40px;
}
.desktop {
  line-height: 20px;
  background-color: #fff;
}
.desktop .ant-layout-header {
  background: transparent;
}
.desktop .ant-layout {
  background: transparent;
}
.desktop .ant-drawer-title {
  margin: 0;
  color: #222;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}
.desktop .narrative {
  box-sizing: border-box;
  text-align: center;
  padding: 0.325rem 1.25rem;
}
.desktop .narrative p {
  margin-bottom: 0.625rem;
  padding: 0.325rem 3.25rem;
  font-size: 16px;
}
.desktop .title {
  -webkit-transition: opacity 0.1s ease;
  transition: opacity 0.1s ease;
}
.desktop .content {
  background-color: transparent;
  width: 100vw;
  margin-top: 72px;
  padding: 0 36px;
}
.desktop .content .logo-line {
  text-align: center;
  background-color: #fff;
}
.desktop .content .logo {
  width: 120px;
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
}
.desktop .content .select-menu {
  background: #fff;
  padding: '30px';
}
.desktop .content .heading {
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
}
.desktop .content .menu {
  text-align: center;
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.desktop .content .menu-drawer {
  text-align: center;
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.desktop .content .menu-drawer .menu-type {
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.desktop .content .menu-drawer .contact-block {
  padding-top: 0;
  padding-left: 17px;
  padding-right: 17px;
  padding-bottom: 17px;
  outline: none;
  box-shadow: inset 0 0 0 1px rgba(128, 128, 128, 0);
  -webkit-transition: box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out;
  clear: none;
  position: relative;
  height: auto;
  word-wrap: break-word;
}
.desktop .content .menu-drawer .h3-block {
  background-color: #ad8094;
  height: 10px;
  width: 40%;
  margin: 0 auto;
  text-align: center;
  font-weight: 600;
  font-style: normal;
  font-size: 1rem;
  letter-spacing: 0.1em;
  line-height: 1.6em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.75);
}
.desktop .content .menu-drawer .contact-detail {
  margin-top: 15px;
}
.desktop .content .menu-display {
  background-color: #fff;
  padding: 0 30px;
}
.desktop .content .menu-display .menu-display-time {
  color: rgba(150, 146, 146, 0.5);
  font-style: normal;
  font-size: 16px;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-align: center;
}
.desktop .content .menu-display .menu-display-category {
  font-family: 'Lobster', cursive;
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
}
.desktop .content .menu-display .menu-display-name {
  margin-top: 18px;
  padding-top: 18px;
  border-top: 1px solid #121212;
  font-family: 'Muli', sans-serif;
  text-align: left;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.25rem;
  line-height: 32px;
  font-weight: 200;
}
.desktop .content .menu-display .menu-display-description {
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.05em;
  font-size: 1rem;
  line-height: 28px;
}
.desktop .content .menu-display .menu-price {
  text-align: left;
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.01em;
  font-size: 0.875rem;
  font-weight: 200;
}
.desktop .content .menu-display-last {
  padding-bottom: 1rem;
}
.desktop .content .contact-display {
  background-color: #fff;
  padding: 0 30px;
  padding: 1.5rem 0;
}
.desktop .content .press-title {
  margin-top: 18px;
  padding: 28px 0;
  font-family: 'Muli', sans-serif;
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
  border-bottom: 1px solid #121212;
}
.desktop .content .press-headline {
  margin-top: 18px;
  padding-top: 18px;
  border-top: 1px solid #121212;
  font-family: 'Muli', sans-serif;
  text-align: left;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.25rem;
  line-height: 32px;
  font-weight: 200;
}
.desktop .content .press-description {
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.05em;
  font-size: 1rem;
  line-height: 28px;
}
.desktop .content .press-logo {
  margin-bottom: 1rem;
}
.desktop .content .carousel {
  padding: 30px;
  background-color: rgba(0, 0, 0, 0.5);
}
.desktop .content .carousel .carosel-image {
  width: 320px;
  height: auto;
  margin: 0 auto;
  padding-bottom: 12px;
}
.desktop .content .carousel .carosel-image .cover {
  margin-bottom: 1em;
}
.desktop .content .carousel .carosel-image-small {
  width: 240px;
  height: auto;
  margin: 0 auto;
  padding-left: 12px;
}
@media only screen and (max-width: 768px) {
  .desktop .content .carousel .carosel-image-small {
    width: 350px;
    height: auto;
    padding-bottom: 12px;
  }
}
.desktop .content .carousel .carosel-image-large {
  width: 520px;
  height: auto;
  margin: 0 auto;
  padding-bottom: 12px;
}
@media only screen and (max-width: 480px) {
  .desktop .content .carousel .carosel-image-large {
    width: 350px;
    height: auto;
  }
}
.desktop .content .carousel .carosel-image-insert {
  width: 350px;
  height: auto;
  margin: 0 auto;
  margin-bottom: 12px;
}
.desktop .content .carousel .describe {
  margin-top: 30px;
  letter-spacing: 0.05em;
  font-size: 0.875rem;
  line-height: 28px;
}
.desktop .gutter-example .ant-row > div {
  background: transparent;
  border: 0;
}
.desktop .gutter-box {
  background: #00a0e9;
  padding: 5px 0;
}
.desktop .press-display-card {
  background: #ececec;
  padding: 30px;
}
@media only screen and (max-width: 480px) {
  .desktop .press-display-card {
    padding: 0px;
  }
  .desktop .press-display-card .press-title-col {
    padding: 0px;
  }
  .desktop .press-display-card .press-title {
    background: #ececec;
  }
}
.thumbnail {
  margin-top: 18px;
  display: inline-block;
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
  text-align: center;
}
.thumbnail img {
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.thumbnail img.portrait {
  width: 100%;
  height: auto;
}
.mobile {
  background-image: none;
}
.mobile .underline-60:after {
  text-align: center !important;
  box-sizing: border-box;
  width: 60px;
  margin: 14px auto;
  content: '';
  display: block;
  height: 4px;
  background-color: #262626;
}
.mobile .mobile-header {
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-flow: row nowrap;
  padding: 0 0.625rem;
  background-color: white;
}
.mobile .mobile-header .logo {
  height: 64px;
}
.mobile .mobile-header .call-us {
  line-height: 64px;
  font-family: 'Source Sans Pro', sans-serif;
  padding-right: 0.625rem;
}
.mobile .mobile-header .call-us a {
  color: #222;
}
.mobile .mobile-header .call-us .phone-number {
  padding-left: 0.625rem;
}
.mobile .mobile-content-display .narrative .text {
  font-size: 16px;
}
.mobile .mobile-content-display .narrative .menu-display-description {
  font-size: 16px;
}
.mobile .narrative {
  box-sizing: border-box;
  padding: 0 1rem;
  text-align: center;
  padding: 0.325rem 1.25rem;
}
.mobile .narrative .menu-button {
  margin-bottom: 0.625rem;
}
.mobile .chef {
  margin-bottom: 0.625rem;
}
.mobile .chef h1 {
  text-align: center;
}
.mobile .chef .chef-picture {
  height: auto;
}
.mobile .chef .chef-quote {
  font-size: 20px;
  padding: 8px 36px;
  margin: 0;
}
.mobile .chef .chef-title {
  font-size: 16px;
  padding: 8px 48px;
  margin: 0;
}
.mobile .chef .chef-title .chef-name {
  font-weight: 700;
}
.mobile .breadcrumb {
  padding: 0.625rem 0.25rem;
  text-align: left;
}
.mobile .contact {
  background: #262626;
  padding: 0.325rem 0.125rem;
}
.mobile .contact .footer-block {
  margin: 50px 0;
  padding: 0 0.125rem;
  line-height: 1.25em;
  color: #fff;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  letter-spacing: 2px;
}
.mobile .contact .text-align-center {
  text-align: center;
}
.mobile .menu-display-category {
  font-family: 'Lobster', cursive;
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
}
.mobile .menu-display-name {
  letter-spacing: 0.05em;
  font-size: 1.25rem;
}
.mobile .menu-display-description {
  font-size: 0.925rem;
}
.mobile .menu-price {
  font-size: 0.875rem;
}
.website .intro-gallery {
  padding-top: 32px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
}
.website .intro-gallery .images {
  display: inline-block;
}
.website .desktop-narrative,
.website .desktop-in-the-news {
  text-align: center;
}
.website p {
  font-size: 18px;
  letter-spacing: 0.05em;
  padding: 0 24px;
}
.website .drawer-activation {
  margin-top: 32px;
}
.website .row-header {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
}
.website .row-header .cell {
  display: flex;
  flex: 50% 1;
  height: 40px;
}
.website .row-header .cell .the-restaurant-logo {
  cursor: pointer;
  padding-left: 24px;
  flex: 1 1;
}
.website .row-header .cell .the-restaurant-logo .logo {
  width: 84px;
  cursor: pointer;
  display: inline-block;
}
.website .row-header .cell .call-the-restaurant {
  cursor: pointer;
  text-align: right;
  width: 100%;
  line-height: 84px;
  letter-spacing: 0.25em;
  flex: 1 1;
  padding-right: 24px;
}
.website .row-header .cell .call-the-restaurant .phone-number {
  padding-left: 0.5rem;
}
.website .footer {
  height: 200px;
  background: #262626;
  color: #fff;
  padding-top: 60px;
}
.website .row-footer {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background: #262626;
}
.website .row-footer .cell {
  display: flex;
  flex: 50% 1;
  height: 40px;
}
.website .row-footer .cell .address,
.website .row-footer .cell .hours,
.website .row-footer .cell .phone-number {
  cursor: pointer;
  line-height: 1.25em;
  color: #fff;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  letter-spacing: 2px;
  flex: 1 1;
  text-align: center;
}
.website .copyright {
  padding-top: 24px;
  color: rgba(235, 186, 186, 0.8);
  text-align: center;
  font-style: normal;
  font-size: 12px;
  letter-spacing: 3px;
  text-transform: uppercase;
}
.website .row-menu-bar {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background: #fff;
  color: #262626;
  height: 2rem;
}
.website .row-menu-bar .cell {
  display: flex;
  flex: 50% 1;
  height: 40px;
}
.website .row-menu-bar .cell .lunch,
.website .row-menu-bar .cell .dinner {
  cursor: pointer;
  line-height: 1.25em;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  letter-spacing: 2px;
  flex: 1 1;
  text-align: center;
}
.flex-wrapper .flex-row {
  display: flex;
  align-content: center;
  justify-content: space-around;
  flex-flow: row nowrap;
}
.flex-row-about-chef {
  display: flex;
  align-content: center;
  justify-content: space-between;
  flex-flow: row nowrap;
  width: 100%;
}
.flex-row-about-chef .flex-col .chef-picture {
  height: auto;
}
.flex-row-about-chef .flex-col .chef-quote {
  font-size: 24px;
}
.flex-row-about-chef .flex-col .chef-title {
  font-size: 20px;
}
.flex-row-about-chef .flex-col .chef-title .chef-name {
  font-weight: 700;
}
.desktop-in-the-news {
  margin-top: 16px;
}
.desktop-in-the-news .news-list {
  padding: 16px;
  border-bottom: 1px solid #e8e8e8;
}
.desktop-in-the-news .news-list .news-card .flex-row {
  display: flex;
}
.desktop-in-the-news .news-list .news-card .flex-col .news-avatar {
  width: 16px;
}
